@import "bootstrap/bootstrap";
@import "AdminLte/skins/skin-blue";
@import "fonts.less";
@import "variables.less";
@import "AdminLte/AdminLTE";
body{
  font-family: @font-family-sans-serif;
  font-size: 14px;
}
/* H1 - H6 font */
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: @headings-font-family;
}
header{
  .logout{
    i{
      font-size: 1.4em;
    }
  }
}
.layout-boxed{
  background: #fafdff;
  background-image: none;
}
.os-form-group {
  label {
    float: left;
    display: block;
    padding-right: 10px;
    margin: 0;
  }
  .input-container {
    overflow: hidden;
  }
  .line-input {
    width: 100%;
    padding: @padding-base-vertical 0;
    -webkit-appearance: none;
    border: none;
    border-radius: 0;
    &:focus {
      outline: none;
    }
  }
  hr {
    margin: 0;
  }
  .has-error {
    hr {
      color: red;
    }
  }
  .message {
    float: left;
    span {
      padding: 10px 0 0 5px;
      font-size: 22px;
    }
  }
}
.main-header{
  .organization-name{
    float: left;
    color: #fff;
    margin: 18px 0 0 0;
  }
}
.list{
  td{
    vertical-align: middle;
  }
  .actions{
    text-align: center;
    vertical-align: middle;
    .action{
      cursor: pointer;
    }
    &.icons{
      font-size: 1.4em ;
      padding: 0;
      a{
        padding:0 3px;
      }
    }
  }
}
.control-bnts{
  margin-bottom: 10px;
  .add-btn{
    text-transform: uppercase;
  }
}
.document{
  .box-header{
    .os-form-group{margin-top:6px;}
    .edit-btn{
      text-transform: uppercase;
    }
    font-size:1.05em;
    .date{
      &:extend(.bg-light-blue);
      color:#fff;
      border-radius: @border-radius-small;
      padding: 3px;
      font-size: 1.1em;
    }
  }
  .box-body{


  }
}
.os{
  .box-header{
    overflow: hidden;
  }
  .btns{
    float:right;
    .btn{
      font-size: 1.3em;
      text-transform: uppercase;
      padding: 4px 8px;

    }

  }
}
.static{
  .box-body{
    font-size: 16px;
  }
}
.content{
  td{
    vertical-align: middle;
  }
}
.center{
  text-align: center;
}
.big{
  font-size: 1.2em;
}
.right{
  text-align: right;
}
.document_sum{
  font-size: 1.2em;
}
.documents-inner-nav{
  font-size:1.2em;
  margin-bottom: 10px;
}
//Hucks
.h1 .small, .h1 small, .h2 .small, .h2 small, .h3 .small, .h3 small, .h4 .small, .h4 small, .h5 .small, .h5 small, .h6 .small, .h6 small, h1 .small, h1 small, h2 .small, h2 small, h3 .small, h3 small, h4 .small, h4 small, h5 .small, h5 small, h6 .small, h6 small {
  font-weight: 400;
  line-height: 1;
  color: #777;
}
.breadcrumb>.active {
  color: #777;
}
.nav-tabs>li.active>a, .nav-tabs>li.active>a:hover, .nav-tabs>li.active>a:focus {
  color: #fff;
  background-color: #3c8dbc;
  border: 1px solid #3c8dbc;
}